@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
* {
  font-family: "Poppins", sans-serif;
  color: white;
}

body,
h1,
p {
  margin: 0;
  padding: 0;
}
body {
  background-color: hsl(0, 100%, 74%);
  padding: 3em;
  background-image: url("images/bg-intro-desktop.png");
}

main {
  display: flex;
  justify-content: center;
  padding: 3em;
}

.content {
  margin: 7em;
}

.trial {
  background-color: hsl(248, 32%, 49%);
  padding: 15px 10px 15px 5px;
  text-align: center;
  border-radius: 9px;
  font-size: 0.9em;
  width: 85%;
  margin-bottom: 20px;
  margin-left: 15px;
  box-shadow: #b94d4d 0px 6px 3px;
}

form {
  background-color: white;
  border-radius: 8px;
  padding-left: 15px;
  padding-right: 25px;
  padding-top: 10px;
  margin-right: 20px;
  box-shadow: #b94d4d 0px 6px 3px;
}

input[type="text"],
input[type="email"],
input[type="password"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type="submit"] {
  background-color: hsl(154, 59%, 51%);
  padding: 15px 10px 15px 5px;
  margin-top: 5px;
  text-align: center;
  border-radius: 9px;
  font-size: 0.7em;
  color: white;
  font-weight: 500;
}

.form-pg {
  color: hsl(246, 25%, 77%);
  font-size: small;
  text-align: center;
}

a {
  color: red;
  text-decoration: none;
}

@media only screen and (max-width: 375px) {
  body {
    background-image: url("images/bg-intro-mobile.png");
    padding: 2.5em;
  }
  main {
    display: grid;
    grid-template-columns: 1fr;
    padding: 1.5em;
  }

  /* .content h1 {
    font-size: 20px;
  } */
  .content p {
    font-size: 0.9em;
    font-weight: 500;
    margin: 1em;
  }
}
